<template>
  <view class="custom-tab-bar">
    <view
      v-for="(item, index) in list"
      :key="index"
      class="tab-item"
      :class="{ active: selected === index }"
      @click="switchTab(index)"
    >
      <image
        :src="selected === index ? item.selectedIconPath : item.iconPath"
        class="icon"
      />
      <text class="text">{{ item.text }}</text>
    </view>
    <!-- 中间扫码按钮 -->
    <!-- <view class="scan-button" @click="handleScan">
      <image src="/static/tabbar/scan.png" class="scan-icon" />
    </view> -->
  </view>
</template>

<script>
export default {
  data() {
    return {
      selected: 0, // 当前选中的 Tab 索引
      list: [
        {
          pagePath: '/pages/home/home',
          text: '工作台',
          iconPath: '/static/img/home.png',
          selectedIconPath: '/static/img/home.png',
        },
        {
          pagePath: '/pages/main/main',
          text: '扫码',
          iconPath: '/static/img/all.png',
          selectedIconPath: '/static/img/all.png',
        },
        {
          pagePath: '/pages/user/user',
          text: '个人中心',
          iconPath: '/static/img/user.png',
          selectedIconPath: '/static/img/user.png',
        },
      ],
    };
  },
  methods: {
    // 切换 Tab
    switchTab(index) {
      this.selected = index;
      uni.switchTab({
        url: this.list[index].pagePath,
      });
    },

    // 处理扫码
    handleScan() {
      uni.scanCode({
        success: (res) => {
          console.log('扫码结果:', res.result);
          uni.showToast({
            title: '扫码成功',
            icon: 'success',
          });
        },
        fail: (err) => {
          console.error('扫码失败:', err);
          uni.showToast({
            title: '扫码失败',
            icon: 'none',
          });
        },
      });
    },
  },
};
</script>

